.App {
  padding:40px;
}

.item{
  margin-bottom:10px;
}

.item span{
  display: inline-block;
  padding:10px;
  background: #eee;
  border-radius:5px;
}

svg path{
  stroke:none;
  stroke-width:2;
}

/* slick */
.slick-list{
  height:200px;
}
.slick-track{
  height:200px;
}
.slick-slide {
  background: #eee;
  margin:0 10px;
  text-align:center;
  border-radius:5px;
}


/* swiper */
.swiper-container{
  height:200px;
}

.swiper-slide{
  background: #eee;
  border-radius:5px;
}


